//åˆå§‹åŒ–
import {INCREMENT,REDUCE} from '../constant'
const initState = 0
export default function count(preState = initState, action) {
    //ä»actionå¯¹è±¡ä¸­è·å–ï¼štypeã€data
    const {type, data} = action
    switch (type) {
        case INCREMENT:
            return preState + data
        case REDUCE:
            return preState - data
        default:
            return preState
    }
}